.navbar {
    margin-top: 0 ;
    background: #26343f;
    height: 60px;
    border-bottom: 5px solid #48E5C2;
    display: flex;
    align-items: center;
    z-index: 2;
}

.navbar ul {
    list-style: none;
    display: flex;
    
}

.navbar ul li a {
    color: #f2f2f2;
    padding: 19px;
    text-decoration: none;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    font-weight: 600;
}

.navbar ul li a:hover {
    color: #95C623;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
  }

  .selected {
    color: #95C623;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }


@media only screen and (max-width: 767px) {
    .navbar {
        padding-top: 70px;
        position: fixed;
        top: 0;
        height: auto;
        width: 100%;
        justify-content: center;
        -webkit-transition: -webkit-transform 0.3s ease-out;
        transition: -webkit-transform 0.3s ease-out;
        transition: transform 0.3s ease-out;
        transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    .mobile-open{
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }

    .navbar ul {
        padding: 0;
        flex-direction: column;

    }

    .navbar ul li {
        font-size: 2em;
        margin: 10px 0;
    }
}

