.projects-grid {
    justify-items: center;
    grid-gap: 0;
  justify-content: center;
   display: grid;
   grid-template-columns: repeat(3, 350px);
}

.project-container {
    height: 180px;
  
      position: relative;
  }

  .project-container:hover  {
    cursor: pointer;
  }

  .project-container img {
    margin: 0;
    height: 180px;
   width: 350px;
   -webkit-transition: 0.5s ease;
   transition: 0.5s ease;
  }

  .project-container:hover .project-description {
    opacity: 1;
  }

  .project-description {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
         margin:0;
        background-color: white;
        width: 350px;
        height: 180px;
        -webkit-transition: .1s ease;
        transition: .1s ease;
        opacity: 0;
        position: absolute;
        top:0;

  }

  .text {
  
    color: #333;
    
    font-size: 0.8em;
    -webkit-transition: color .2s linear,  -webkit-transform 0.3s ease-out;
    transition: color .2s linear,  -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out,  color .2s linear;
    transition: transform 0.3s ease-out,  color .2s linear,  -webkit-transform 0.3s ease-out;
    -webkit-transform: translateY(-40%);
            transform: translateY(-40%);
  
  }

  .project-container:hover .text{
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }

  .project-title {
    font-size: 1.3em;
  }
  
  .link a, .project-title{
      color: #48E5C2;
    text-decoration: none;
    font-weight: bold;
  }

  .link {
    bottom: 0;
    border-radius: 50px;
    width: 150px;
    padding-top:5px;
    padding-bottom:8px;
    margin-top: 5%;
    border: 2px solid #48E5C2;
    -webkit-transition: 0.3s, -webkit-transform 0.3s ease-out;
    transition: 0.3s, -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out, 0.3s;
    transition: transform 0.3s ease-out, 0.3s, -webkit-transform 0.3s ease-out;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    
  }

  .link:hover {
    background: #48E5C2;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  
  }
  .link a {
    color: #333;
  }
  
  .link:hover a {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color: white;
  }

  .project-container:hover .link {
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }

  @media only screen and (max-width: 700px) {
    .projects-grid {
      grid-template-columns: repeat(1, 350px);
    }
    #portfolio {
      padding-top: 100px;
    }
  }
